/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

* {
  box-sizing: inherit;
  transition: all 0.3s linear;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.8rem;
}

h4 {
  font-size: 2.5rem;
}

p {
  font-size: 2rem;
}

img {
  width: 20rem;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 3.5%;
  background-color: #ffffff;
  transition: all 0.5s linear;
}
.header.hd-activo {
  background-color: #000000;
}
.header.hd-activo .logo h1 {
  color: #ffffff;
}
.header.hd-activo .navegacion a {
  color: #ffffff;
}
.header.hd-activo #menu-btn {
  color: #ffffff;
}
.header .logo {
  display: flex;
  align-items: center;
}
.header .logo:hover {
  transform: scale(1.05);
}
.header .logo img {
  width: 6rem;
  height: 6rem;
}
.header .logo h1 {
  font-size: 2.5rem;
  color: #000000;
}
.header .logo h1 span {
  font-weight: 400;
}
.header .navegacion {
  display: flex;
  gap: 1.5rem;
}
.header .navegacion a {
  font-size: 2rem;
  font-weight: 500;
  color: #000000;
}
.header .navegacion a:hover {
  transform: scale(1.05);
}
.header #menu-btn {
  font-size: 3.5rem;
  cursor: pointer;
  display: none;
}

@media (max-width: 1200px) {
  html {
    font-size: 55%;
  }
}
@media (max-width: 991px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 768px) {
  .header #menu-btn {
    display: inline-block;
  }
  .header #menu-btn.fa-times {
    transform: rotate(180deg);
  }
  .header .navegacion {
    position: absolute;
    top: 99%;
    right: 0;
    width: 50%;
    height: 100vh;
    flex-direction: column;
    row-gap: 4.5rem;
    align-items: center;
    border-bottom: 0.5px solid #000000;
    background-color: #000000;
    clip-path: polygon(100% 0, 100% 0, 100% 100%, 100% 100%);
  }
  .header .navegacion.activo {
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  }
  .header .navegacion a {
    color: #ffffff;
    font-weight: 600;
    text-transform: uppercase;
  }
  .header .navegacion a:first-of-type {
    margin-top: 4.5rem;
  }
}
.hero {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url(/NitidoNewPage/src/images/backgrounds/hero.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.hero .sombra {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  /*transparencia*/
  display: flex;
  align-items: center;
}
.hero-contenido {
  position: absolute;
  top: 52%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}
.hero-contenido h2 {
  text-transform: uppercase;
  font-size: 4.5rem;
  color: #ffffff;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
  line-height: 1.2;
  margin-bottom: 6rem;
}
@media (max-width: 450px) {
  .hero-contenido h2 {
    font-size: 3.5rem;
  }
}
.hero-contenido a {
  display: inline-block;
  background-color: #000000;
  color: #ffffff;
  padding: 1rem 5rem;
  font-size: 2rem;
  border-radius: 1rem;
  position: relative;
}
.hero-contenido a:hover {
  transform: scale(1.05);
}

.nosotros {
  position: relative;
  top: 99%;
  width: 100%;
  height: 100vh;
  padding: 7rem 4%;
  display: flex;
  align-items: center;
}
.nosotros .grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2.5rem;
  align-items: center;
  justify-content: center;
}
.nosotros .grid .imagen img {
  width: 100%;
  vertical-align: middle;
}
.nosotros .grid .texto h3 {
  background-color: #256edc;
  display: inline-block;
  font-weight: 400;
  font-size: 2.5rem;
  padding: 1rem 1.5rem;
  color: #ffffff;
}
.nosotros .grid .texto p {
  margin: 3rem 0;
  line-height: 1.7;
  text-align: justify;
}
.nosotros .grid .texto .redes-sociales {
  display: flex;
  column-gap: 1rem;
}
.nosotros .grid .texto .redes-sociales img {
  width: 6rem;
  height: 6rem;
}
.nosotros .grid .texto .redes-sociales img:hover {
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .nosotros {
    height: auto;
  }
  .nosotros .grid {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .nosotros .grid .texto {
    text-align: center;
  }
  .nosotros .grid .texto p {
    margin: 3rem 0;
  }
  .nosotros .grid .texto .redes-sociales {
    justify-content: center;
  }
}
.catalogo {
  position: relative;
  width: 100%;
  padding: 5rem 0;
  text-align: center;
}
.catalogo h2 {
  color: #ffffff;
  display: inline-block;
  width: 100%;
  padding: 1rem 2.8rem;
  font-size: 2.8rem;
  font-weight: 200;
  background-color: #000000;
}
.catalogo .grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  justify-content: center;
}
.catalogo .grid .producto {
  position: relative;
  overflow: hidden;
  cursor: pointer;
  border-bottom: 0.1px solid #000000;
  border-right: 0.1px solid #000000;
}
.catalogo .grid .producto img {
  display: block;
  width: 100%;
  aspect-ratio: 1/1;
  object-fit: cover;
  object-position: center;
}
.catalogo .grid .producto .contenido {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.4);
}
.catalogo .grid .producto .contenido p {
  font-size: 2rem;
  padding: 1rem;
}
.catalogo .grid .producto img,
.catalogo .grid .producto .contenido {
  transition: 200ms ease-in-out;
}
.catalogo .grid .producto > .contenido {
  opacity: 0;
}
.catalogo .grid .producto:hover > .contenido {
  opacity: 1;
}
.catalogo .grid .producto:hover .filtro {
  filter: blur(5px);
}